import BN from 'bn.js'
import ms from 'ms'
import Web3 from 'web3'
import { EventOptions } from 'web3-eth-contract'
import { toBN } from 'web3-utils'
import { fromDecimal, toBytes32 } from './utils'

import { abi } from '@makerdao-cdp-insurance/smart-contracts-abi/contracts/PolicyService.json'

import ServiceController from './ServiceController'

import SmartContract from './SmartContract'

type PolicyEvent = ActivationEvent | ChangeEvent | CancellationEvent | EraseEvent | ExecutionEvent

interface ActivationEvent {
  event: 'ProtectionActivated'
  cdp: string
  activationDate: BN
  duration: BN
  owner: string
  policy: string
  price: BN
  liquidationPenalty: BN
  debt: BN
}

interface ChangeEvent {
  event: 'ProtectionChanged'
  cdp: string
  activationDate: BN
  duration: BN
  owner: string
  policy: string
  price: BN
  liquidationPenalty: BN
  debt: BN
}

interface CancellationEvent {
  event: 'ProtectionCanceled'
  cdp: string
  owner: string
  activationDate: BN
  sender: string
}

interface EraseEvent {
  event: 'ProtectionErased'
  cdp: string
  owner: string
  activationDate: BN
  sender: string
}

interface ExecutionEvent {
  event: 'ProtectionExecuted'
  cdp: string
  owner: string
  activationDate: BN
  sender: string
}

export interface CdpProtection {
  cdpId: number
  activation: number
  duration: number
  expiration: number
  debt: BN
  liquidationPenalty: BN
  owner: string
  policy: string
  price: BN
}

export interface EventData {
  activation: number
  blockNumber: number
  cdp: number
  owner: string
  timestamp: number
  debt?: BN
  duration?: number
  liquidationPenalty?: BN
  policy?: string
  price?: BN
}

export class PolicyService extends SmartContract {
  static async build(web3: Web3): Promise<PolicyService> {
    const serviceController = await ServiceController.build(web3)
    const policyServiceAddress = await serviceController.getServiceAddress('PolicyService')
    const networkId = await web3.eth.net.getId()
    const networks = {
      [networkId]: {
        address: policyServiceAddress,
        transactionHash: ''
      }
    }

    const builder = PolicyService.configure({ contractName: 'PolicyService', abi, networks })
    const options = await builder(web3)

    return new PolicyService(options)
  }

  async getActiveProtections(options?: EventOptions) {
    const policies: CdpProtection[] = []

    const events = await this.contract.getPastEvents('allEvents', {
      fromBlock: this.initialBlock,
      ...options
    })

    events
      .filter(
        ({ event }) =>
          event === 'ProtectionActivated' ||
          event === 'ProtectionCanceled' ||
          event === 'ProtectionChanged' ||
          event === 'ProtectionErased' ||
          event === 'ProtectionExecuted'
      )
      .reduce((cdpEvents, { event, returnValues }) => {
        const data = returnValues as PolicyEvent

        if (!cdpEvents.has(data.cdp)) {
          cdpEvents.set(data.cdp, [])
        }

        cdpEvents.get(data.cdp).push({ event, ...data })

        return cdpEvents
      }, new Map<string, PolicyEvent[]>())
      .forEach(cdpEvents => {
        const newestEvent = cdpEvents[cdpEvents.length - 1]

        if (
          newestEvent.event === 'ProtectionActivated' ||
          newestEvent.event === 'ProtectionChanged'
        ) {
          const cdpId = toBN(newestEvent.cdp.toString()).toNumber()
          const activation = toBN(newestEvent.activationDate.toString()).toNumber() * ms('1s')
          const duration = toBN(newestEvent.duration.toString()).toNumber()
          const expiration = activation + duration * ms('1d')

          policies.push({
            activation,
            cdpId,
            debt: toBN(newestEvent.debt.toString()),
            duration,
            expiration,
            liquidationPenalty: toBN(newestEvent.liquidationPenalty.toString()),
            owner: newestEvent.owner,
            policy: newestEvent.policy,
            price: toBN(newestEvent.price.toString())
          })
        }
      })

    return policies
  }

  async getActivatedEvents(options?: EventOptions) {
    const events = await this.contract.getPastEvents('ProtectionActivated', {
      fromBlock: this.initialBlock,
      ...options
    })

    const activationEvents: EventData[] = await Promise.all(
      events.map(async ({ event, returnValues, blockNumber }) => {
        const timestamp = (await this.web3.eth.getBlock(blockNumber)).timestamp * ms('1s')

        const data = returnValues as ActivationEvent

        return {
          activation: toBN(data.activationDate.toString()).toNumber() * ms('1s'),
          blockNumber,
          cdp: toBN(data.cdp.toString()).toNumber(),
          debt: toBN(data.debt.toString()),
          duration: toBN(data.duration.toString()).toNumber(),
          liquidationPenalty: toBN(data.liquidationPenalty.toString()),
          owner: data.owner,
          policy: data.policy,
          price: toBN(data.price.toString()),
          timestamp
        }
      })
    )

    return activationEvents
  }

  async getChangeEvents(options?: any) {
    const events = await this.contract.getPastEvents('ProtectionChanged', {
      fromBlock: this.initialBlock,
      ...options
    })

    const adjusmentEvents: EventData[] = await Promise.all(
      events.map(async ({ event, returnValues, blockNumber }) => {
        const timestamp = (await this.web3.eth.getBlock(blockNumber)).timestamp * ms('1s')

        const data = returnValues as ChangeEvent

        return {
          activation: toBN(data.activationDate.toString()).toNumber() * ms('1s'),
          blockNumber,
          cdp: toBN(data.cdp.toString()).toNumber(),
          debt: toBN(data.debt.toString()),
          duration: toBN(data.duration.toString()).toNumber(),
          liquidationPenalty: toBN(data.liquidationPenalty.toString()),
          owner: data.owner,
          policy: data.policy,
          price: toBN(data.price.toString()),
          timestamp
        }
      })
    )

    return adjusmentEvents
  }

  async getCancellationEvents(options?: any) {
    const events = await this.contract.getPastEvents('ProtectionCanceled', {
      fromBlock: this.initialBlock,
      ...options
    })

    const cancelEvents: EventData[] = await Promise.all(
      events.map(async ({ event, returnValues, blockNumber }) => {
        const timestamp = (await this.web3.eth.getBlock(blockNumber)).timestamp * ms('1s')

        const data = returnValues as CancellationEvent

        return {
          activation: toBN(data.activationDate.toString()).toNumber() * ms('1s'),
          blockNumber,
          cdp: toBN(data.cdp.toString()).toNumber(),
          owner: data.sender,
          timestamp
        }
      })
    )

    return cancelEvents
  }

  async getEraseEvents(options?: any) {
    const events = await this.contract.getPastEvents('ProtectionErased', {
      fromBlock: this.initialBlock,
      ...options
    })

    const eraseEvents: EventData[] = await Promise.all(
      events.map(async ({ event, returnValues, blockNumber }) => {
        const timestamp = (await this.web3.eth.getBlock(blockNumber)).timestamp * ms('1s')

        const data = returnValues as EraseEvent

        return {
          activation: toBN(data.activationDate.toString()).toNumber() * ms('1s'),
          blockNumber,
          cdp: toBN(data.cdp.toString()).toNumber(),
          owner: data.owner,
          timestamp
        }
      })
    )

    return eraseEvents
  }

  async getExecutionEvents(options?: any) {
    const events = await this.contract.getPastEvents('ProtectionExecuted', {
      fromBlock: this.initialBlock,
      ...options
    })

    const executionEvents: EventData[] = await Promise.all(
      events.map(async ({ event, returnValues, blockNumber }) => {
        const timestamp = (await this.web3.eth.getBlock(blockNumber)).timestamp * ms('1s')

        const data = returnValues as ExecutionEvent

        return {
          activation: toBN(data.activationDate.toString()).toNumber() * ms('1s'),
          blockNumber,
          cdp: toBN(data.cdp.toString()).toNumber(),
          owner: data.owner,
          timestamp
        }
      })
    )

    return executionEvents
  }

  async protectionPrice(cdpId: number, debt: number, duration: number = 30) {
    const prices = await this.contract.methods
      .protectionPrice(toBytes32(cdpId), fromDecimal(debt), duration)
      .call()

    return prices
  }
}

export default PolicyService
