import { toBN, toHex, toWei } from 'web3-utils'

const padLeft = (toPad: any, chars: any, sign?: any) => {
  return new Array(chars - toPad.length + 1).join(sign ? sign : '0') + toPad
}

export const toBytes32 = (x: any, prefix = true) => {
  let y = toHex(x)
  y = y.replace('0x', '')
  y = padLeft(y, 64)
  if (prefix) {
    y = '0x' + y
  }

  return y
}

export function fromDecimal(value: number) {
  try {
    return toWei(toBN(value).toString()).toString()
  } catch (e) {
    return ''
  }
}
