import {
  abi,
  networks
} from '@makerdao-cdp-insurance/smart-contracts-abi/contracts/ServiceController.json'
import Web3 from 'web3'

import SmartContract from './SmartContract'

class ServiceController extends SmartContract {
  static async build(web3: Web3): Promise<ServiceController> {
    const builder = ServiceController.configure({
      abi,
      contractName: 'ServiceController',
      networks
    })
    const options = await builder(web3)

    return new ServiceController(options)
  }

  async getServiceAddress(service: string) {
    const serviceAddress = await this.contract.methods
      .lookup(this.web3.utils.fromAscii(service))
      .call()
    return serviceAddress
  }
}

export default ServiceController
