import { abi } from '@makerdao-cdp-insurance/smart-contracts-abi/contracts/UserProxyRegistry.json'
import Web3 from 'web3'

import ServiceController from './ServiceController'
import SmartContract from './SmartContract'

export class UserProxyRegistry extends SmartContract {
  static async build(web3: Web3): Promise<UserProxyRegistry> {
    const serviceController = await ServiceController.build(web3)
    const userProxyRegistryAddress = await serviceController.getServiceAddress('UserProxyRegistry')
    const networkId = await web3.eth.net.getId()
    const networks = {
      [networkId]: {
        address: userProxyRegistryAddress,
        transactionHash: ''
      }
    }

    const builder = UserProxyRegistry.configure({
      abi,
      contractName: 'UserProxyRegistry',
      networks
    })
    const options = await builder(web3)

    return new UserProxyRegistry(options)
  }

  async getUserProxies(owner: string) {
    const proxies = await this.contract.methods.userProxies(owner).call()

    return proxies
  }
}

export default UserProxyRegistry
